import React from 'react';
import DoList from './NotepadComponents/DoList.js';
import DoneList from './NotepadComponents/DoneList.js';
import TaskInput from './NotepadComponents/TaskInput.js';

// The main component. State is managed here.

class NotePad extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      section: 'To do',
      alphabeticalOrder: false,
      importantOrder: false,
      doArray: [
        {
          name: 'Welcome to this minimal to-do list!',
          emoji: "ðŸ¤—",
          important: false,
        },
        {
          name: 'Click on me to complete the to-do',
          emoji: "âœ…",
          important: true,
        },
        {
          name: 'Click on the To-do button in the upper left corner to see your completed tasks',
          emoji: "ðŸ‘‰",
          important: false,
        },
      ],
      doneArray: [
        {
          name: "If you enjoy this project, don't forget to tweet the creator, @4rnoldGee!",
          emoji: "ðŸ’™",
          important: true,
        },
      ],
      userInput: '',
      randomEmoji: '\u{1F4D3}',
    };
    this.handleChangeSection = this.handleChangeSection.bind(this);
    this.handleSortByAlphabeticalOrder = this.handleSortByAlphabeticalOrder.bind(
      this
    );
    this.handleSortByImportant = this.handleSortByImportant.bind(this);
    this.handleWriteTask = this.handleWriteTask.bind(this);
    this.handleAddToDo = this.handleAddToDo.bind(this);
    this.handleCompleteToDo = this.handleCompleteToDo.bind(this);
    this.handleReviveToDo = this.handleReviveToDo.bind(this);
    this.handleMarkAsImportant = this.handleMarkAsImportant.bind(this);
    this.getRandomEmoji = this.getRandomEmoji.bind(this);
    this.autoSave = this.autoSave.bind(this);
    this.handleClearDone = this.handleClearDone.bind(this);
  }

  componentDidMount() {
    // FALTA REVISAR
    if (window.localStorage.getItem('doArray')) {
      this.setState({
        doArray: JSON.parse(window.localStorage.getItem('doArray')),
      });
    }
    if (window.localStorage.getItem('doneArray')) {
      this.setState({
        doneArray: JSON.parse(window.localStorage.getItem('doneArray')),
      });
    }
  }
  autoSave() {
    window.localStorage.setItem('doArray', JSON.stringify(this.state.doArray));
    window.localStorage.setItem(
      'doneArray',
      JSON.stringify(this.state.doneArray)
    );
  }
  handleClearDone() {
    const deleteConfirmation = window.confirm(
      'Do you want to delete all the completed tasks?'
    );
    if (deleteConfirmation) {
      this.setState({
        doneArray: [],
      });
      this.autoSave();
    }
  }

  handleChangeSection() {
    this.setState((prevState) => ({
      section: prevState.section === 'To do' ? 'Done' : 'To do',
    }));
  }

  handleSortByAlphabeticalOrder() {
    this.setState((previousState) => ({
      alphabeticalOrder: !previousState.alphabeticalOrder,
    }));
  }
  handleSortByImportant() {
    this.setState((previousState) => ({
      importantOrder: !previousState.importantOrder,
    }));
  }

  handleWriteTask(event) {
    this.setState({
      userInput: event.target.value,
    });
  }
  handleAddToDo(event) {
    event.preventDefault();
    if (this.state.userInput !== '') {
      this.setState((prevState) => ({
        doArray: [
          {
            name: prevState.userInput,
            emoji: prevState.randomEmoji,
            important: false,
          },
          ...prevState.doArray,
        ],
        userInput: '',
      }));
      this.getRandomEmoji();
      this.autoSave();
    }
  }
  getRandomEmoji() {
    fetch('https://ranmoji.herokuapp.com/emojis/api/v.1.0/').then(
      (response) => {
        response.json().then((data) => {
          this.setState({
            randomEmoji: data.emoji,
          });
        });
      }
    );
  }

  handleCompleteToDo(event) {
    const {doArray, doneArray} = this.state;
    const toDoPosition = +event.target.title;

    let newDoneArray = [doArray[toDoPosition], ...doneArray];
    let newDoArray = doArray
      .slice(0, toDoPosition)
      .concat(doArray.slice(toDoPosition + 1));

    this.setState({
      doArray: newDoArray,
      doneArray: newDoneArray,
    });
    this.autoSave();
  }

  handleReviveToDo(event) {
    const {doArray, doneArray} = this.state;
    const toDoPosition = +event.target.title;

    let newDoArray = [doneArray[toDoPosition], ...doArray];
    //alert(newDoArray)
    let newDoneArray = doneArray
      .slice(0, toDoPosition)
      .concat(doneArray.slice(toDoPosition + 1));

    this.setState({
      doArray: newDoArray,
      doneArray: newDoneArray,
    });
    this.autoSave();
  }

  handleMarkAsImportant(event) {
    const toDoPosition = +event.target.title;
    const newToDo = {
      name: this.state.doArray[toDoPosition].name,
      emoji: this.state.doArray[toDoPosition].emoji,
      important: !this.state.doArray[toDoPosition].important,
    };
    this.setState((prevState) => ({
      doArray: [
        ...prevState.doArray.slice(0, toDoPosition),
        newToDo,
        ...prevState.doArray.slice(toDoPosition + 1),
      ],
    }));
    this.autoSave();
  }

  render() {
    const {
      section,
      doArray,
      doneArray,
      userInput,
      alphabeticalOrder,
      importantOrder,
    } = this.state;

    return (
      <main className="container-fluid vw-100 d-flex justify-content-center mb-5">
        <div id="notePad" className="w-100 p-4 shadow-lg">
          <div className="d-flex mb-4 justify-content-between">
            <button
              id="section-title"
              className={
                section === 'To do'
                  ? 'btn border-0 btn-outline-dark btn-lg'
                  : 'btn border-0 btn-dark btn-lg'
              }
              onClick={this.handleChangeSection}
            >
              {section}
            </button>
            <div>
              <span className="text-muted" style={{marginBottom: '0px'}}>
                Sort by:
              </span>
              <div className="btn-group ml-4" role="group">
                <button
                  className={
                    alphabeticalOrder
                      ? 'btn btn-info btn-lg'
                      : 'btn btn-outline-info btn-lg'
                  }
                  onClick={this.handleSortByAlphabeticalOrder}
                >
                  Aa
                </button>
                {section === 'To do' ? (
                  <button
                    className={
                      importantOrder
                        ? 'btn btn-info btn-lg'
                        : 'btn btn-outline-info btn-lg'
                    }
                    onClick={this.handleSortByImportant}
                  >
                    <i className="far fa-star"></i>
                  </button>
                ) : (
                  <button
                    className={
                      importantOrder
                        ? 'btn btn-danger btn-lg'
                        : 'btn btn-outline-danger btn-lg'
                    }
                    onClick={this.handleClearDone}
                  >
                    <i class="fa fa-trash-o" aria-hidden="true"></i>
                  </button>
                )}
              </div>
            </div>
          </div>

          {section === 'To do' ? (
            <DoList
              doArray={doArray}
              handleCompleteToDo={this.handleCompleteToDo}
              handleMarkAsImportant={this.handleMarkAsImportant}
              alphabeticalOrder={alphabeticalOrder}
              importantOrder={importantOrder}
            />
          ) : (
            <DoneList
              doneArray={doneArray}
              handleReviveToDo={this.handleReviveToDo}
              alphabeticalOrder={alphabeticalOrder}
            />
          )}

          <TaskInput
            userInput={userInput}
            handleWriteTask={this.handleWriteTask}
            handleAddToDo={this.handleAddToDo}
          />
        </div>
      </main>
    );
  }
}

export default NotePad;
