import React from 'react';

const DoneList = (props) => {
  return (
    <ul className="w-100 list-group list-group-flush">
      {props.doneArray
        .map((item, index) => {
          return Object.assign(item, {index});
        })
        .sort((a, b) => {
          // sorts by alphabetical order, if props.alphabeticalOrder === true
          if (props.alphabeticalOrder) {
            let nameA = a.name.toUpperCase();
            let nameB = b.name.toUpperCase();

            if (nameA > nameB) {
              return 1;
            }
            if (nameA < nameB) {
              return -1;
            }
          }
          // a must be equal to b
          return 0;
        })
        .map((item) => (
          <li
            className="to-do-element w-100 list-group-item d-flex"
            title={item.index}
            onClick={props.handleReviveToDo}
            key={item.index}
          >
            <span
              className="mr-3 animated-emoji"
              dangerouslySetInnerHTML={{__html: item.emoji}}
            ></span>
            {item.name}
          </li>
        ))}
    </ul>
  );
};

export default DoneList;
