import React from 'react';

const TaskInput = (props) => {
  return (
    <form className="form-group d-flex mb-0 mt-3" onSubmit={props.handleAddToDo}>
      
        <input
          type="text"
          value={props.userInput}
          className="container-fluid form-control-lg border-0"
          onChange={props.handleWriteTask}
          placeholder="Create to-do"
        ></input>
      
      <button className="btn btn-outline-success btn-lg border-0" type="submit">
      <i className="fas fa-magic"></i>
      </button>
    </form>
  );
};

export default TaskInput;
