import React from 'react';

const DoList = (props) => {
  return (
    <ul className="w-100 list-group list-group-flush">
      {props.doArray
        .map((item, index) => {
          return Object.assign(item, {index});
        })
        .sort((a, b) => {
          // sorts by alphabetical order, if props.alphabeticalOrder === true
          if (props.alphabeticalOrder) {
            let nameA = a.name.toUpperCase();
            let nameB = b.name.toUpperCase();

            if (nameA > nameB) {
              return 1;
            }
            if (nameA < nameB) {
              return -1;
            }
          }
          // a must be equal to b
          return 0;
        })
        .sort((a, b) => {
          // sorts by order of importance, if (props. importantOrder === true)
          if (props.importantOrder) {
            if (!a.important && b.important) {
              return 1;
            } else if (a.important && !b.important) {
              return -1;
            }
          }
          return 0;
        })
        .map((item) => (
          <li
            className=" list-group-item d-flex justify-content-between align-items-center"
            key={item.index}
          >
            <span
              className="to-do-element w-100 d-flex justify-content-start align-items-center"
              title={item.index}
              onClick={props.handleCompleteToDo}
            >
              <p
                className="mr-3 animated-emoji mb-0"
                dangerouslySetInnerHTML={{__html: item.emoji}}
              ></p>
              <p className="mb-0">{item.name}</p>
            </span>
            <i
              title={item.index}
              className={
                item.important
                  ? 'fas fa-star important-task'
                  : 'far fa-star unimportant-task'
              }
              onClick={props.handleMarkAsImportant}
            ></i>
          </li>
        ))}
    </ul>
  );
};

export default DoList;
